import axios from "axios";
// 导入loading Loading 服务
import { Message } from 'element-ui'
import {BASE_ADDRESS, TIME_OUT, TOKEN_ERROR } from './config'
import {getToken} from "@/utils/auth";
export function request(config){
    const service =axios.create({
        baseURL:BASE_ADDRESS, //配置公共接口
        timeout:TIME_OUT //配置请求超时时间
    })
    // 请求拦截器
    service.interceptors.request.use(config=> {
        config.headers['token'] = getToken()
        return config
    },() => {

    })

    // 响应拦截器
    service.interceptors.response.use(res=>{
        if(res.status === 200 && res.data.error_code === 200){
            return res
        }else  if (res.data.error_code === TOKEN_ERROR){
            Message({
                type: "warning",
                message: '登录失效，请重新登录！',
                center: true
            })
        } else {
            // 其他错误
            Message({
                type: "error",
                message: res.data.reason,
                center: true
            })
        }
        return res
    },err =>{
        return err
    })
    return service(config)
}

export function requestCallback(request, success, error) {
    request.then((res) => {
        if(res.status === 200 && res.data.error_code === 200){
            success(res.data)
        }else  if (res.data.error_code === TOKEN_ERROR){
            error(res.data)
        } else {
            error(res.data)
        }
    }).catch(() => {
        error(null)
    })
}